@import url(https://fonts.googleapis.com/css2?family=Permanent+Marker&family=Shadows+Into+Light&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#footer{
  background-image:   linear-gradient( lightgreen 1%, white 99%);
  z-index: 10;
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 2.5rem;            /* Footer height */
}

#foot-credit{
  float: right;
  padding-right: 2vw;
}

@media screen and (max-width: 992px) {
  #foot-credit{
    float: left;
    padding-left: 5vw;
  }
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.tpage .jumbotron{
    background-image:   linear-gradient(180deg, lightgreen, white);
}

.tpage .jumbotron .container h1{
    font-family: 'Shadows Into Light', cursive;
    font-size: 100px;
    font-weight: bolder;
    background: -webkit-linear-gradient(white, #aaa, #333);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.tpage .container-tpage{
    width: 65%;
}

.tpage .tpage-active-sum{
    justify-content: center;
}

.tpage .tpage-btns-row{
    justify-content: space-evenly;
}

.tpage .tpage-btns-row .btn{
    margin-top: 5px;
    color: lightgreen;
    border-color: lightgreen;
    text-align: center;
    width: 100%;
}
.r-TodoCard{
    width: 100%;
    margin: 0;
    margin-top: 5px;
    padding: 0;
    justify-content: center;
}

.r-TodoCard .card{
    width: 100%;
    height: 50px;
    align-items: center;
    flex-direction: row;
    text-align: left;

}

.r-TodoCard .form-check-input:checked + .form-check-label{
    -webkit-text-decoration-line: line-through;
            text-decoration-line: line-through;
}

.r-TodoCard .card .btn{
    display: none;
    margin-left: auto;
    border-radius: 50%;
    color: red;
    background-color: rgba(220, 220, 220, 0.363);
    border-color: transparent;
}

.r-TodoCard:hover .btn{
    display: inline-block;
}
